"use strict";

document.addEventListener("DOMContentLoaded", function (e) {
  console.log("Script loaded successfully");
  // Get the modal
  var modal = document.getElementById("modal");

  // Get the button that opens the modal
  var getStartedBtn = document.getElementById("get-started-button");

  // Get the next button
  var nextBtn = document.getElementById("next-btn");

  // Get the message button
  var messageBtn = document.getElementById("message-btn");

  // Get the <span> element that closes the modal
  var span = document.getElementById("close-modal");

  // Get the message form
  var messageForm = document.getElementById("send-message-form");

  // Get the start shipping form
  var shippingForm = document.getElementById("start-shipping-form");

  // Get the success message form
  var successMessageForm = document.getElementById("success-message-form");

  // When the user clicks on the next button, display the message form
  nextBtn.addEventListener("click", function () {
    // Get the forms and change display styling
    shippingForm.style.display = "none";
    successMessageForm.style.display = "block";
  });
  messageBtn.addEventListener("click", function () {
    var baseURL = window.location.origin; // Get the base URL
    var contactURL = new URL("contact", baseURL); // Construct the new URL
    window.location.href = contactURL.href;
  });

  // When the user clicks on the button, open the modal
  getStartedBtn.onclick = function () {
    modal.style.display = "flex";
  };

  // When the user clicks on <span> (x), close the modal
  span.onclick = function () {
    modal.style.display = "none";
  };

  // When the user clicks anywhere outside of the modal, close it
  window.onclick = function (event) {
    if (event.target == modal) {
      modal.style.display = "none";
    }
  };

  // When the user clicks on the "next" button, display the form
  nextBtn.onclick = function () {
    modal.style.display = "flex";
  };
  var emailInput = document.getElementById("email");
  var emailError = document.getElementById("email-error");
  emailInput.addEventListener("blur", function () {
    var email = emailInput.value;
    var emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailRegex.test(email)) {
      emailError.style.display = "block";
    } else {
      emailError.style.display = "none";
    }
  });
  var fNameInput = document.getElementById("first-name");
  var fNameError = document.getElementById("first-name-error");
  fNameInput.addEventListener("blur", function () {
    var fName = fNameInput.value;
    if (fName == "" || fName.length < 3) {
      fNameError.style.display = "block";
    } else {
      fNameError.style.display = "none";
    }
  });
});

